export default {
	data() {
		return {
			list: [],
			now_page: 1,
			size: 10,
			total: '',
		}
	},
	onLoad() {
		this.getList();
	},
	onReachBottom() { //监听上拉触底事件
		// this.showLoadMore = true;
		if (this.list.length < this.total) {
			uni.showToast({
				icon: 'loading',
				title: '正在加载',
				duration: 1000
			})
			this.now_page += 1;
			setTimeout(() => {
				this.$http.get('/petbnb/article/getArticleList', {
						index: this.now_page,
						size: this.size
					})
					.then(res => {
						this.freshList = res.data.list;
						this.list = this.list.concat(this.freshList);
					})
					.catch(err => {
						uni.showToast({
							icon: 'none',
							title: '系统错误，请稍后再试'
						})
					})
			}, 1000)
		} else {
			uni.showToast({
				icon: 'none',
				title: '暂无更多数据'
			})
		}
	},
	methods: {
		getList() {
			this.$http.get('/petbnb/article/getArticleList', {
					index: this.now_page,
					size: this.size
				})
				.then(res => {
					this.total = res.data.total;
					this.list = res.data.list;
			})
		}
	}
}
